





































import { Vue, Component } from 'vue-property-decorator'
import { adminGetMessage } from 'utils/api'

@Component({})
export default class ViewMessage extends Vue {
  messages: any = []
  selections: any = []
  editForm: any = {
    index: '',
    title: '',
    content: ''
  }
  editFormRules: any = {
    title: [
      { required: true, message: '请填写标题', trigger: 'blur' }
    ],
    content: [
      { required: true, message: '请填写内容', trigger: 'blur' }
    ]
  }
  editFormVisible: boolean = false

  mounted () {
    adminGetMessage()
      .then((res) => {
        if (res.data) {
          res.data.forEach((datum) => {
            datum.releasedTime = datum.releasedTime.split('T')[0]
            this.messages.push(datum)
          })
        } else {
          this.$message({
            message: '读取失败',
            type: 'error'
          })
        }
      })
      .catch((err) => {
        this.$message({
          message: err,
          type: 'error'
        })
      })
  }

  handleSelectionChange (val) {
    this.selections = val
  }

  editMessage (index, row) {
    this.editFormVisible = true
    this.editForm = {
      index: index,
      title: row.title,
      content: row.content
    }
  }

  deleteMessage (index) {
    this.$confirm('确认删除？')
      .then(() => {
        this.messages.splice(index, 1)
      })
      .catch(() => {
      })
  }

  // editSubmit () {
  //   this.messages[this.editForm.index].title = this.editForm.title
  //   this.messages[this.editForm.index].content = this.editForm.content
  //   adminUpdateMessage({
  //     messages: this.messages
  //   }).then((res) => {
  //     if (res.data.succeed) {
  //       this.$message.success('编辑成功')
  //     } else {
  //       this.$message.error(res.data.msg)
  //     }
  //   }).catch((err) => {
  //     this.$message.error('编辑失败')
  //   })
  // }

  handleEditClose () {
    this.$confirm('确认关闭？')
      .then(() => {
        this.editFormVisible = false
      })
      .catch(() => {
      })
  }
}



